.hero {
    height: 70vh;
    width: 100%;
    background-color: #8BC6EC;
    background-image: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%);
    /*background-position: center;
    background-repeat: no-repeat;
    background-size: cover;*/

    .wrapper {
        article {
            text-align: center;
            top: 50%;
            transform: translateY(-50%);
            position: relative;
            h1 {
                font-size: 78px;
                color: $dark-color;
                text-transform: uppercase;
                margin-bottom: 28px;

            }
            h2 {
                font-family: Georgia;
                font-style: italic;
                font-size: 23px;
                letter-spacing: 0.05em;
                text-indent: 1px;
                margin-bottom: 28px;


            }
            .btn {
                margin-top: 35px;

            }
        }
    }
}