.grid {
    .wrapper {
        .grid-container {
            display: grid;
            grid-template-columns: 320p 320px 320px 320px;
            grid-template-rows: 320px 320px 320px;
            
            div {
                text-align: center;
                background-color: #ccc;
                &:nth-last-of-type(odd) {
                    background: #666;
                }
                &:nth-of-type(1) {
                    grid-row-start: 1;
                    grid-row-end: 3;
                    background-color: #eee;
                }
                &:nth-of-type(7) {
                    grid-column-start: 4;
                    grid-column-end: 5;
                    grid-row-start: 2;
                    grid-row-end: 4;
                    background-color: #eee;
                }
                article {
                    position: relative;
                    top: 50%;
                    transform: translateY(-50);
                }
            }
        }
    }
}