footer {
    padding: 60px 0 60px 0;
    .wrapper {
        font-size: 0;
        background-color: yellow;
        .coll {
            display: inline-block;
            vertical-align: top;
            width: 100%;
            max-width: 25%;
            font-size: 20px;
            ul {
                li {
                    font-size: 13px;
                    color: $dark-color;
                    &:first-child {
                        font-size: 15px;
                        font-weight: 600;
                        text-transform: uppercase;
                        margin-bottom: 15px;
                    }
                    a {
                        color: $dark-color;
                        &:hover {
                            text-decoration: underline;
                        }

                    }

                }
            }
        }

    }
}