.signup {
    padding: 130px 0;
    background-color: #8BC6EC;
    background-image: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%);
    .wrapper {
        text-align: center;
        h2 {
            font-size: 38px;
            font-weight: 600;
            color: $dark-color;
            margin-bottom: 10px;
        
        }
        p {
            font-size: 19px;
            font-style: italic;
            color: $dark-color;
        }
        .form {
            font-size: 0;
            max-width: 570px;
            margin: 50px auto 0 50px;
            display: inline-block;
            input[type="text"] {
                border-top: 1px solid $dark-color;
                border-left: 1px solid $dark-color;
                border-bottom: 1px solid $dark-color;
                border-right: none;
                color: $dark-color;
                line-height: 30px;
                display: inline-block;
                padding: 0 10px 0 10px;
                font-size: 14px;
                text-transform: uppercase;

            }
            input[type="submit"] {
                background-color: $secondary-color;
                font-size: 14px;
                border: 1px solid $secondary-color;
                color: $light-color;
                line-height: 30px;
                text-align: center;
                display: inline-block;
                text-transform: uppercase;
                cursor: pointer;


            }

        }
    }


}